<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="120px"
			>
			<el-form-item class="add-item" label="" prop="baoxiudanhao">
				<el-input v-model="ruleForm.baoxiudanhao" 
					placeholder="" clearable :disabled=" false  ||ro.baoxiudanhao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="baoxiumingcheng">
				<el-input v-model="ruleForm.baoxiumingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.baoxiumingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="baoxiuleixing">
				<el-input v-model="ruleForm.baoxiuleixing" 
					placeholder="" clearable :disabled=" false  ||ro.baoxiuleixing"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.fengmian)" prop="fengmian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.fengmian?ruleForm.fengmian:''"
					@change="fengmianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="fengmian">
				<img v-if="ruleForm.fengmian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.fengmian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.fengmian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="baoxiudidian">
				<el-input v-model="ruleForm.baoxiudidian" 
					placeholder="" clearable :disabled=" false  ||ro.baoxiudidian"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shenqingshijian">
				<el-date-picker
					:disabled=" false  ||ro.shenqingshijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.shenqingshijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="zhanghao">
				<el-input v-model="ruleForm.zhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.zhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xingming">
				<el-input v-model="ruleForm.xingming" 
					placeholder="" clearable :disabled=" false  ||ro.xingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shouji">
				<el-input v-model="ruleForm.shouji" 
					placeholder="" clearable :disabled=" false  ||ro.shouji"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="wanchengshijian">
				<el-date-picker
					:disabled="true  ||ro.wanchengshijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.wanchengshijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="weixiushizhang">
				<el-input v-model.number="ruleForm.weixiushizhang" 
					placeholder="" clearable :disabled=" false  ||ro.weixiushizhang"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="weixiuzhanghao">
				<el-input v-model="ruleForm.weixiuzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.weixiuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="weixiuxingming">
				<el-input v-model="ruleForm.weixiuxingming" 
					placeholder="" clearable :disabled=" false  ||ro.weixiuxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="lianxifangshi">
				<el-input v-model="ruleForm.lianxifangshi" 
					placeholder="" clearable :disabled=" false  ||ro.lianxifangshi"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="weixiujieguo">
				<editor 
					v-model="ruleForm.weixiujieguo" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					baoxiudanhao : false,
					baoxiumingcheng : false,
					baoxiuleixing : false,
					fengmian : false,
					baoxiudidian : false,
					shenqingshijian : false,
					zhanghao : false,
					xingming : false,
					shouji : false,
					wanchengshijian : false,
					weixiushizhang : false,
					weixiuzhanghao : false,
					weixiuxingming : false,
					lianxifangshi : false,
					weixiujieguo : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					baoxiudanhao: '',
					baoxiumingcheng: '',
					baoxiuleixing: '',
					fengmian: '',
					baoxiudidian: '',
					shenqingshijian: '',
					zhanghao: '',
					xingming: '',
					shouji: '',
					wanchengshijian: '',
					weixiushizhang: '',
					weixiuzhanghao: '',
					weixiuxingming: '',
					lianxifangshi: '',
					weixiujieguo: '',
				},


				rules: {
					baoxiudanhao: [
					],
					baoxiumingcheng: [
					],
					baoxiuleixing: [
					],
					fengmian: [
					],
					baoxiudidian: [
					],
					shenqingshijian: [
					],
					zhanghao: [
					],
					xingming: [
					],
					shouji: [
						{ validator: this.$validate.isMobile, trigger: 'blur' },
					],
					wanchengshijian: [
					],
					weixiushizhang: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					weixiuzhanghao: [
					],
					weixiuxingming: [
					],
					lianxifangshi: [
						{ validator: this.$validate.isMobile, trigger: 'blur' },
					],
					weixiujieguo: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
			this.ruleForm.shenqingshijian = this.getCurDateTime()
			this.ruleForm.wanchengshijian = this.getCurDateTime()
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='baoxiudanhao'){
							this.ruleForm.baoxiudanhao = obj[o];
							this.ro.baoxiudanhao = true;
							continue;
						}
						if(o=='baoxiumingcheng'){
							this.ruleForm.baoxiumingcheng = obj[o];
							this.ro.baoxiumingcheng = true;
							continue;
						}
						if(o=='baoxiuleixing'){
							this.ruleForm.baoxiuleixing = obj[o];
							this.ro.baoxiuleixing = true;
							continue;
						}
						if(o=='fengmian'){
							this.ruleForm.fengmian = obj[o].split(",")[0];
							this.ro.fengmian = true;
							continue;
						}
						if(o=='baoxiudidian'){
							this.ruleForm.baoxiudidian = obj[o];
							this.ro.baoxiudidian = true;
							continue;
						}
						if(o=='shenqingshijian'){
							this.ruleForm.shenqingshijian = obj[o];
							this.ro.shenqingshijian = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
						if(o=='shouji'){
							this.ruleForm.shouji = obj[o];
							this.ro.shouji = true;
							continue;
						}
						if(o=='wanchengshijian'){
							this.ruleForm.wanchengshijian = obj[o];
							this.ro.wanchengshijian = true;
							continue;
						}
						if(o=='weixiushizhang'){
							this.ruleForm.weixiushizhang = obj[o];
							this.ro.weixiushizhang = true;
							continue;
						}
						if(o=='weixiuzhanghao'){
							this.ruleForm.weixiuzhanghao = obj[o];
							this.ro.weixiuzhanghao = true;
							continue;
						}
						if(o=='weixiuxingming'){
							this.ruleForm.weixiuxingming = obj[o];
							this.ro.weixiuxingming = true;
							continue;
						}
						if(o=='lianxifangshi'){
							this.ruleForm.lianxifangshi = obj[o];
							this.ro.lianxifangshi = true;
							continue;
						}
						if(o=='weixiujieguo'){
							this.ruleForm.weixiujieguo = obj[o];
							this.ro.weixiujieguo = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.weixiuzhanghao!=''&&json.weixiuzhanghao) || json.weixiuzhanghao==0){
							this.ruleForm.weixiuzhanghao = json.weixiuzhanghao;
							this.ro.weixiuzhanghao = true;
						}
						if((json.weixiuxingming!=''&&json.weixiuxingming) || json.weixiuxingming==0){
							this.ruleForm.weixiuxingming = json.weixiuxingming;
							this.ro.weixiuxingming = true;
						}
						if((json.lianxifangshi!=''&&json.lianxifangshi) || json.lianxifangshi==0){
							this.ruleForm.lianxifangshi = json.lianxifangshi;
							this.ro.lianxifangshi = true;
						}
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`weixiujieguo/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`weixiujieguo/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			fengmianUploadChange(fileUrls) {
				this.ruleForm.fengmian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

